window.onload = function() {
    var deliveryRadios = document.getElementsByName('delivery');
    var novaPoshtaField = document.getElementById('novaPoshtaField');
    var courierField = document.getElementById('courierField');
    for (var i = 0, length = deliveryRadios.length; i < length; i++) {
      deliveryRadios[i].addEventListener('change', function() {
        novaPoshtaField.style.display = 'none';
        courierField.style.display = 'none';
        if (this.value === 'nova_poshta') {
          novaPoshtaField.style.display = 'block';
        } else if (this.value === 'courier') {
          courierField.style.display = 'block';
        } 
      });
    }
    for (var i = 0, length = deliveryRadios.length; i < length; i++) {
      if (deliveryRadios[i].checked) {
        deliveryRadios[i].dispatchEvent(new Event('change'));
        break;
      }
    }
  }